/* width */
::-webkit-scrollbar {
   width: 4px;
}

/* Track */
::-webkit-scrollbar-track {
   background-color: transparent;
   border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
   background: #ccc;
   border-radius: 10px;
}
body{
   width: 100%;
   height: 100vh;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   background-color: #fff;
   font-family: 'Montserrat', sans-serif;
}

.light{
   background-color: #fff;
}
.dark{
   background-color: #222327;
   color: #fff ;
}




header{
   position: relative;
   padding: 30px 40px;
   width: 550px;
   height: 500px;
   box-shadow: 0 14px 28px rgba(107, 107, 107, 0.25),
   0 10px 10px rgba(145, 144, 144, 0.22);
   border-radius: 10px;
   border: 1px solid rgba(221, 221, 221, 0.205);
   background-color: #fff;
   // overflow-y: auto;

   .icons {
      position: absolute;
      top: 20px;
      right: 30px;
      background-color: transparent;
      border: none;
      cursor: pointer;

      .iconSun {
         display: block;
         font-size: 32px;

      }

      .iconMoon {
         display: block;
         font-size: 32px;
         color: rgb(255, 254, 254);
      }

      .click__icon {
         display: none;
      }
   }
   
   .selectComplite{
      position: absolute;
      font-family: 'Montserrat',sans-serif;
      width: 150px;
      top: 0;
      right: -160px;
      outline: none;
      padding: 5px 10px;
      border-radius: 5px;
      border: 1px solid rgba(221, 221, 221, 0.205);
      box-shadow: 0 2px 10px rgba(107, 107, 107, 0.25);
   }

   .header__top{
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-bottom: 30px;
      border-bottom: 2px solid rgba(170, 170, 170, .5);
      margin-bottom: 30px;

      .header__write {
         h1 {
            font-size: 32px;
            line-height: 24px;
            color: #3E4356;
            margin-bottom: 24px;
         }
            .input {
               width: 300px;
               padding: 10px 20px;
               border: none;
               outline: none;
               border-bottom: 2px solid #3e4356be;
               margin-bottom: 5px;
               font-family: 'Montserrat';
               font-weight: 400;
               font-size: 16px;
               color: #555;
               
            }
            .count{
               font-size: 12px;
               color: #777;
               margin-bottom: 10px;
               
            }
      }
      .img{
         width: 100px;
         height: 100px;
         border-radius: 50%;
         box-shadow: 0 0px 20px rgba(158, 157, 157, 0.25);
      }
      .header__text{
         font-weight: 500;
         color: #999;
         .color{
            color: #055353;
            letter-spacing: 1.2px;
            font-weight: 700;
            .stik{
               color: rgb(16, 17, 99);
            }
         }
      }
   }

   .list{
      height: 235px;
      overflow-y: auto;
      .list__item{
         border-bottom: 2px solid rgba(62, 67, 86, 0.5);
         padding: 20px 10px;
         display: flex;
         justify-content: space-between;
         align-items: center;
         transition: opacity ease-in-out .3s;
         .list__btn{
            cursor: pointer;  
            padding: 7px 20px;
            color: #fff;
            background-color: #E65B65;
            border: none;
            border-radius: 5px;
            font-size: 18px;
            line-height: 20px;
         }
      }
      .list__item.complite{
         opacity: .6;
      }

      .list__text{
         font-weight: 500;
         color: #012929;
         flex: 1 1;
         padding: 0 10px;
         transition: ease-in-out .3s;
         .index{
            font-weight: 700;
            font-size: 18px;
            color: #39966C;
         }
      }
      .list__text.line{
         text-decoration: line-through;
      }
   }

}
