.checkbox {
   position: relative;
   height: 20px;
   width: 20px;
   -webkit-appearance: none;
   -moz-appearance: none;
   -o-appearance: none;
   appearance: none;
   border: 1px solid #34495E;
   border-radius: 4px;
   outline: none;
   transition-duration: 0.3s;
   background-color: #fff;
   cursor: pointer;
}

.checkbox:checked {
   border: 1px solid #41B883;
   background-color: #34495E;
}

.checkbox:checked::before {
   content: '\2713';
   position: absolute;
   display: block;
   text-align: center;
   color: #41B883;
   font-size: 18px;
   left: 2px;
   top: -2px;
}

.checkbox:active {
   border: 2px solid #34495E;
}