*, *::before, *::after {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: sans-serif;
  font-size: 16px;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  display: inline-block;
}

.container {
  width: 1200px;
  margin: 0 auto;
  padding: 10px 0;
}

body.dark .header{
  color: #fff;
  background-color: #23262D;
  box-shadow: 0 14px 28px rgba(61, 61, 61, 0.25),
  0 10px 10px rgba(51, 50, 50, 0.22) !important;
}
body.dark h1{
  color: #fff !important;
}
body.dark .input{
  background-color: #23262D;
  color: #AEB1B4 !important;
  border-bottom: 2px solid #eee !important;
}
body.dark .count {
  color: #fff !important;
}
body.dark .header__text {
  color: #ccc !important;
}
body.dark .color {
  color: #fff !important;
}
body.dark .stik {
  color: #fff !important;
}
body.dark .list__text {
  color: #fff !important;
}
body.dark .list__item {
  border-color: #999 !important;
}
body.dark .selectComplite {
  background-color: #23262D !important;
  color: #fff !important;
}


